@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow {
    margin-bottom: 30px;
}
.movieRow h2 {
    margin: 0px 0px 0px 30px;
}
.movieRow--listArea {
    overflow-x: hidden;
    padding-left: 30px;
}
.movieRow--list {
    width: 99999999px;
}
.movieRow--item {
    display: inline-block;
    width: 150px;
    cursor: pointer;
}
.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}
.movieRow--item img:hover {
    transform: scale(1);
}
*{
  box-sizing: border-box;
}

body{
  background-color: black;
  color: white;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists{
  margin-top: -60px;
}

footer{
  margin: 50px;
  text-align: center;
}

.footer--link{
  color: red;
  text-decoration: none;
}
.featured {
    height: 100vh;
}
.featured--vert {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, black 10%, transparent 90%);
}
.featured--horiz {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, black 10%, transparent 60%);
    padding-left: 30px;
    padding-top: 150px;

}
.featured--name {
    font-size: 60px;
    font-weight: bold;
}
.featured--info {
    font-size:  18px;
    font-weight: bold;
    margin-top: 15px;
}
.featured--points {
    display: inline-block;
    color: #46d369;
    margin-right: 15px;
}
.featured--year {
    display: inline-block;
    margin-right: 15px;
}
.featured--seasons {
    display: inline-block;
    margin-right: 15px;
}
.featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}
.featured--buttons {
    margin-top: 20px;
}
.watch--button,
.list--button{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
}
.watch--button:hover,
.list--button:hover{
    opacity: 0.7;
}
.watch--button{
    background-color: white;
    color: black;
}
.list--button{
    background-color: #333;
    color: white;
}
.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}
header{
    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: transparent;
    padding-right: 30px;
    padding-left: 30px;
}

.header--black{
    background-color: #141414;
    transition: all ease 0.4s;
}

.header--logo,
.header--user {
    height: 40px;
}

.header--logo img,
.header--user img {
    height: 100%;
}
